import {useState, useEffect} from 'react'
import firebase from './Firebase'
const Item = () => {

    const db = firebase.firestore();

    const [orderNum, setOrderNum]  = useState(0);  
    
    useEffect(() => {
        db.collection("ordernumber").doc("num")
        .onSnapshot((doc) => {
            setOrderNum(doc.data().ordernum);
        });
    }, []);
   

    
    const subtractPina = ()=>{
        if (pina > 0){
            setPina(pina-1);
        }
    }
    const subtractraspberry = ()=>{
        if (raspberry > 0){
            setRaspberry(raspberry-1);
        }
    }
    const subtractLime = ()=>{
        if (lime > 0){
            setLime(lime-1);
        }
    }
    const subtractCherry = ()=>{
        if (cherry > 0){
            setCherry(cherry-1);
        }
    }
    const subtractCandy = ()=>{
        if (candy > 0){
            setCandy(candy-1);
        }
    }
    const subtractDust = ()=>{
        if (dust > 0){
            setDust(dust-1);
        }
    }
    const subtractBow = ()=>{
        if (bow > 0){
            setBow(bow-1);
        }
    }
    const [pina, setPina] = useState(0);
    const [raspberry, setRaspberry] = useState(0);
    const [lime, setLime] = useState(0);
    const [cherry, setCherry] = useState(0);
    const [candy, setCandy] = useState(0);
    const [dust, setDust] = useState(0);
    const [bow, setBow] = useState(0);


    const [name, setName] = useState(null);

    const handleName= (val) => {
        setName(val.target.value);
    }
     

    const SendOrder = () =>{
        const total = 3 * (pina + raspberry + lime + cherry + candy + dust + bow); 
        
        if (total > 0){
            let temp = orderNum + 1;
            db.collection("ordernumber").doc("num").set({
            ordernum: temp
            })
            
            db.collection("orders").add({
            OrderName: name,
            pinaNum: pina,
            raspberryNum: raspberry,
            limeNum: lime,
            cherryNum: cherry,
            bloodNum: candy,
            grapeNum: dust,
            bowNum: bow,
            total: total

            })
            .then((docRef) => {
            alert("Submitted order number of: " + orderNum + " and a price of $"+ total + ". Come to 1235 Brookdale Ave to pick up your order. To cancel the order, call (650)690-0235");
            console.log(name);
            })
        }
        else{
            alert("You cannot order nothing! Click the plus button under the photos to choose")
        }
                
    }

    return (
        <div className="flex wrap1">

            <h3 style = {{marginTop: '3vw'}}>Name for Order: </h3>
            <input placeholder = 'John Doe' type = 'text' style = {{marginTop: '1vw'}} onChange = {handleName}></input>

            <div className="flex wrap2" style = {{padding: '1vw'}}>
                
                <div className="flex wrap1" style = {{padding: '2vw'}}>
                    <h3>Pina Colata</h3>
                    <img className = 'itemIMG' alt = 'photo1'src = "https://media-cdn.tripadvisor.com/media/photo-s/10/73/d9/c6/adult-shave-ice-pina.jpg"></img>
                    <h3>Price: $3.00</h3>
                    <div className="flex wrap2">
                        <button style  = {{margin: '0.5vw'}} onClick = {()=>subtractPina()}>-</button><h3>{pina}</h3><button style  = {{margin: '0.5vw'}} onClick = {()=>setPina(pina+1)}>+</button>
                    </div>
                </div>

                <div className="flex wrap1" style = {{padding: '2vw'}}>
                    <h3>Blue Raspberry</h3>
                    <img className = 'itemIMG' alt = 'photo2'src = "https://media-cdn.tripadvisor.com/media/photo-s/0e/86/21/d7/blue-raspberry-and-coconut.jpg"></img>
                    <h3>Price: $3.00</h3>
                    <div className="flex wrap2">
                        <button style  = {{margin: '0.5vw'}} onClick = {()=>subtractraspberry()}>-</button><h3>{raspberry}</h3><button style  = {{margin: '0.5vw'}} onClick = {()=>setRaspberry(raspberry+1)}>+</button>
                    </div>
                </div>

                <div className="flex wrap1" style = {{padding: '2vw'}}>
                    <h3>Lemon-Lime</h3>
                    <img className = 'itemIMG' alt = 'photo3'src = "https://media-cdn.tripadvisor.com/media/photo-s/11/85/27/e8/small-green-river-and.jpg"></img>
                    <h3>Price: $3.00</h3>
                    <div className="flex wrap2">
                        <button style  = {{margin: '0.5vw'}} onClick = {()=>subtractLime()}>-</button><h3>{lime}</h3><button style  = {{margin: '0.5vw'}} onClick = {()=>setLime(lime+1)}>+</button>
                    </div>
                </div>

                <div className="flex wrap1" style = {{padding: '2vw'}}>
                    <h3>Wild Cherry</h3>
                    <img className = 'itemIMG' alt = 'photo4'src = "https://media-cdn.tripadvisor.com/media/photo-s/09/3f/b1/83/photo0jpg.jpg"></img>
                    <h3>Price: $3.00</h3>
                    <div className="flex wrap2">
                        <button style  = {{margin: '0.5vw'}} onClick = {()=>subtractCherry()}>-</button><h3>{cherry}</h3><button style  = {{margin: '0.5vw'}} onClick = {()=>setCherry(cherry+1)}>+</button>
                    </div>
                </div>
                <div className="flex wrap1" style = {{padding: '2vw'}}>
                    <h3>Tiger's Blood</h3>
                    <img className = 'itemIMG' alt = 'photo5'src = "https://www.mynameissnickerdoodle.com/wp-content/uploads/2020/07/tigers-blood-snow-cone-syrup-8.jpg"></img>
                    <h3>Price: $3.00</h3>
                    <div className="flex wrap2">
                        <button style  = {{margin: '0.5vw'}} onClick = {()=>subtractCandy()}>-</button><h3>{candy}</h3><button style  = {{margin: '0.5vw'}} onClick = {()=>setCandy(candy+1)}>+</button>
                    </div>
                </div>
                <div className="flex wrap1" style = {{padding: '2vw'}}>
                    <h3>Grape</h3>
                    <img className = 'itemIMG' alt = 'photo6'src = "https://media-cdn.tripadvisor.com/media/photo-s/1b/d0/e1/25/regular-sized-grape-hawaiian.jpg"></img>
                    <h3>Price: $3.00</h3>
                    <div className="flex wrap2">
                        <button style  = {{margin: '0.5vw'}} onClick = {()=>subtractDust()}>-</button><h3>{dust}</h3><button style  = {{margin: '0.5vw'}} onClick = {()=>setDust(dust+1)}>+</button>
                    </div>
                </div>
                <div className="flex wrap1" style = {{padding: '2vw'}}>
                    <h3>Rainbow</h3>
                    <img className = 'itemIMG' alt = 'photo7'src = "https://media-cdn.tripadvisor.com/media/photo-s/08/f3/02/a0/peace-love-shave-ice.jpg"></img>
                    <h3>Price: $3.00</h3>
                    <div className="flex wrap2">
                        <button style  = {{margin: '0.5vw'}} onClick = {()=>subtractBow()}>-</button><h3>{bow}</h3><button style  = {{margin: '0.5vw'}} onClick = {()=>setBow(bow+1)}>+</button>
                    </div>
                </div>

            </div>  


            <h1>Total = ${3 * (pina + raspberry + lime + cherry + candy + dust + bow)}</h1>
            <button className = 'fav' onClick = {()=>SendOrder()}style = {{margin: '2vw'}}>Order</button>

        </div>

    )
}

 
export default Item;