import Item from './Item'
import firebase from './Firebase'
import {useState, useEffect} from 'react';


const Order = () => {

    const db = firebase.firestore();

    const [orderNum, setOrderNum]  = useState(0);  
    
    useEffect(() => {
        db.collection("ordernumber").doc("num")
        .onSnapshot((doc) => {
            setOrderNum(doc.data().ordernum);
        });
    }, []);
   
    const OrderMain = () => {
        return (  
            <div className = 'flex wrap1'>
                <h1 style = {{paddingTop: '3vh', color: 'purple'}}>Menu</h1>
                <h3>Order Number: {orderNum}</h3>
                <Item/>
            </div>
        );
    }

   
     
    
    return (  
    <>
        <OrderMain/>
    </>
    
    
    );
}
 
export default Order;