import { BrowserRouter as Router, Route, Switch} from "react-router-dom";
import Navz from './components/Navbar';
import Home from './components/Home';
import Order from './components/Order';
function App() {

  const tag = () =>{
      let url = `https://github.com/Ashwin-T`;
      window.open(url,'_blank');
  }
  return (
    <div className = ''>
      <Router>
            <Navz/>
            <Switch>
                <Route exact path = '/'>
                  <Home/>
                </Route>      
                <Route exact path = '/order'>
                  <Order/>
                </Route>
            </Switch>
            <footer>
              <h2><span onClick = {tag}>Coded by Ashwin Talwalkar</span></h2>
              <h3 style = {{padding: '0'}}>v1.15</h3>
            </footer>

        </Router>
    </div>
  );
}

export default App;
