import {Link} from 'react-router-dom';

const Navz = () => {

    return (  
    <div className="flex wrap2" >
            <Link to = '/'  style = {{textDecoration: 'none'}}><h3 className="bars">Home Page</h3></Link>
            <Link to = '/order' style = {{textDecoration: 'none'}}><h3 className="bars">Order Online</h3></Link>
    </div>
    );
}
 
export default Navz;